//Sidebar
.nav {
  grid-area: n;
  padding: 30px;
  display: flex;
  flex-direction: column;
  background-color: #4A76A8;

  .active {
    color: gold;
  }

  a {
    margin: 7px 0 7px 0;
    display: block;
    color: #fff;
    text-decoration: none;
    font-size: 20px;

    &:last-child {
      margin-top: 50px;
    }
  }
}